<template>
  <div>
    <h3>Projects</h3>
    <div class="cards">
      <div v-for="project in projects" v-bind:key="project.id">
        <md-card class="md-raised">
          <h4 class="md-subheading">{{ project.project_title }}</h4>
          <md-card-header ratio="1:3">
            <md-card-media>
              <img :src="project.image_url" alt="Project screenshot" />
            </md-card-media>

            <md-card-header-text>
              <div class="md-subhead">{{ project.languages }}</div>
            </md-card-header-text>
          </md-card-header>
          <md-card-area>
            <span class="md-caption">{{ project.description }}</span>
          </md-card-area>
          <md-card-actions>
            <md-button
              class="md-icon-button"
              v-on:click="openLink(project.gitHub_link)"
            >
              <i name="hi" class="fab fa-github"></i>
            </md-button>
            <md-button @click="openLink(project.site_url)"
              ><span>Site link</span>
            </md-button>
            <span>{{ project.project_date }}</span>
          </md-card-actions>
        </md-card>
      </div>
    </div>
  </div>
</template>
<style scoped>
.cards {
  display: flex;
  overflow-x: auto;
  overflow-y: hidden;
}
.md-card {
  overflow: hidden;
  cursor: pointer;
  max-width: 500px;
  min-width: 300px;
  margin: 4px;
  min-height: 300px;
  border-radius: 20px;
  vertical-align: top;
  background-color: rgba(27, 27, 27, 0.932);
  border: solid 2px rgb(255, 82, 3);
}
.md-subheading {
  padding: 10px;
  font-size: 1.2rem;
  color: rgb(255, 82, 3);
  font-weight: 800;
}
.md-card-content {
  padding: 5px 10px;
}
.md-card-header {
  padding: 10px;
}
.md-caption {
  font-style: italic;
  font-size: 15px;
  padding: 10px;
  color: rgb(255, 255, 255);
}
.md-card-actions {
  position: absolute;
  bottom: 0px;
  width: 100%;
  height: 30px;
  justify-content: space-around;
  background-color: rgba(209, 119, 2, 0.747);
}
</style>
<script>
import { db } from "../main";
import "vue-material/dist/vue-material.min.css";
export default {
  name: "Projects",
  data() {
    return {
      projects: [],
    };
  },
  methods: {
    openLink: function (url) {
      if (url !== "") {
        window.open(url, "_blank");
      }
    }
  },
  created() {
    db.collection("projects")
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const data = {
            id: doc.id,
            project_title: doc.data().project_title,
            image_url: doc.data().image_url,
            project_date: doc.data().project_date,
            description: doc.data().project_description,
            languages: doc.data().languages,
            gitHub_link: doc.data().gitHub_link,
            site_url: doc.data().site_url,
          };
          this.projects.push(data);
        });
      });
  },
};
</script>
